import React from 'react';
import { Container, Row } from 'reactstrap';
import { Serie } from '../components/Serie/Serie';

export default {
  title: 'Series',
  component: Serie,
};

export const SeriesTest = (props) => {
   return( <Container>
      <Row>
        <Serie reps={ props.reps } name="This is a serie"/>
      </Row>
    </Container>)
};

export const Primary = SeriesTest.bind({});
Primary.args = {
  reps: 5,
};

SeriesTest.storyName = 'Series first example';



